import styled from 'styled-components';
import Button from '../common/Button';
import { useForm } from 'react-hook-form';
import { useEffect, useState } from 'react';
import { formatDate } from '../../lib/format';

const EmailFormBlock = styled.form`
  width: 100%;
  max-width: 768px;
  margin: 32px 0 42px;
`;

const StyleLabel = styled.label`
  position: absolute;
  left: 0;
  top: 0;
  font-size: 1rem;
  line-height: 1.4rem;
  color: var(--oc-gray-6);
`;

const InputBlock = styled.div`
  position: relative;
  padding-top: 24px;
  margin-bottom: 32px;
  input {
    font-size: 1.1rem;
    line-height: 1.4rem;
    outline: none;
    padding: 8px 0;
    border: none;
    border-bottom: 1px solid var(--bd-lv-1);
    width: 100%;
    background: none;
    color: var(--txt-df-color);
  }
`;
const TextAreaBlock = styled.div`
  position: relative;
  padding-top: 24px;
  textarea {
    font-size: 1rem;
    line-height: 1.4rem;
    outline: none;
    padding: 12px;
    border: 1px solid var(--bd-lv-1);
    width: 100%;
    height: 240px;
    resize: none;
    background: none;
    color: var(--txt-df-color);
  }
`;

const ErrMsg = styled.span`
  display: inline-block;
  color: var(--oc-pink-6);
  font-size: 0.9rem;
  line-height: 1.2rem;
  padding-top: 8px;
`;

const emailRegEx =
  /^[A-Za-z0-9]([-_.]?[A-Za-z0-9])*@[A-Za-z0-9]([-_.]?[A-Za-z0-9])*\.[A-Za-z]{2,3}$/;

export default function EmailForm({ onChangeField, onSubmit, resetForm }) {
  const {
    register,
    handleSubmit, //e.preventDefault();
    formState: { errors },
    reset,
  } = useForm({ mode: 'all' });

  const setValueAndSubmit = async (datas) => {
    Object.keys(datas).map((key) =>
      onChangeField({ key: key, val: datas[key] }),
    );
    onChangeField({ key: 'sendTime', val: currentTime });
    onSubmit();
  };
  useEffect(() => {
    if (resetForm) reset();
    console.log('reset run');
    return () => {
      reset();
      console.log('reset clear');
    };
  }, [resetForm, reset]);

  const [currentTime, setCurrentTime] = useState(formatDate(new Date()));

  useEffect(() => {
    const intervalId = setInterval(() => {
      setCurrentTime(formatDate(new Date()));
      // console.log(currentTime);
    }, 1000); // Update every second

    return () => clearInterval(intervalId); // Clear interval on unmount
  }, []);
  return (
    <div>
      <EmailFormBlock onSubmit={handleSubmit(setValueAndSubmit)}>
        <input type="hidden" name="timestamp" value={currentTime} />
        <InputBlock>
          <input
            placeholder=" ..."
            id="name"
            {...register('name', {
              required: '  .',
              maxLength: { value: 5, message: '6  .' },
            })}
            type="text"
          />
          <StyleLabel htmlFor="name">Name</StyleLabel>
          {errors.name && <ErrMsg>{errors.name.message}</ErrMsg>}
        </InputBlock>
        <InputBlock>
          <input
            placeholder=" ..."
            id="email"
            {...register('email', {
              required: '  .',
              pattern: {
                value: emailRegEx,
                message: '   .   .',
              },
            })}
            type="email"
          />
          <StyleLabel htmlFor="email">Email</StyleLabel>
          {errors.email && <ErrMsg>{errors.email.message}</ErrMsg>}
        </InputBlock>
        <InputBlock>
          <input
            placeholder=" ..."
            id="subject"
            {...register('subject', { required: '  .' })}
            type="text"
          />
          <StyleLabel htmlFor="subject">Subject</StyleLabel>
          {errors.subject && <ErrMsg>{errors.subject.message}</ErrMsg>}
        </InputBlock>
        <TextAreaBlock>
          <textarea id="text" {...register('body')} />
          <StyleLabel htmlFor="text">Message</StyleLabel>
        </TextAreaBlock>
        <Button func="true" type="submit">
           
        </Button>
      </EmailFormBlock>
    </div>
  );
}
